#view-container {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    width: 100vw;
    height: 100vh;
    overflow: hidden;

    /*z-index: 1;*/

    margin: 0;
    padding: 0;

    display: -ms-flexbox;

    display: flex;
    -ms-flex-flow: column nowrap;
        flex-flow: column nowrap;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;

    font-family: var(--font-family);
}

#sections-container {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    width: 100%;
    height: 100%;
    overflow-x: hidden;
    overflow-y: auto;
    
    /*
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    align-items: center;
    */
   position: relative;

    display: block;
    margin: 0;
    padding: 0;
}

.effect-container {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    display: inline-block;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content; 
    height: -webkit-max-content; 
    height: -moz-max-content; 
    height: max-content;

    margin: 0;
    padding: 0;
}

.section-header {
    display: block;
    
    padding: 0;
    margin: 0;

    position: relative;
    font-size: var(--section-header-size);
    
    border-style: none;
    color: var(--section-header-fg);
}

.section-header::after {
    content: '';
    position: absolute;
    bottom: -5px;
    left: 25%;

    width: 50%;
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;

    background: none;
    border-style: none none solid none;
    border-width: 3px;
    border-color: black;
}

.btn {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    display: inline-block;

    margin: 0;
    padding: 0.5rem 1rem;
    font-size: 1.3em;
    font-variant: small-caps;

    outline: none;
    border-style: solid;
    border-width: 2px;
    border-radius: 4px;

    color: var(--btn-fg);
    background: var(--btn-bg);
    border-color: var(--btn-border);

    -webkit-transition: background 0.4s;

    -o-transition: background 0.4s;

    transition: background 0.4s;
}

.btn:hover {
    color: var(--btn-hover-fg);
    background: var(--btn-hover-bg);
}

@-webkit-keyframes top-enter {
    from {
        margin-top: 500%;
    }

    to {
        margin-top: 0px;
    }
}

@keyframes top-enter {
    from {
        margin-top: 500%;
    }

    to {
        margin-top: 0px;
    }
}

@-webkit-keyframes bottom-enter {
    from {
        margin-bottom: 500%;
    }

    to {
        margin-bottom: 0px;
    }
}

@keyframes bottom-enter {
    from {
        margin-bottom: 500%;
    }

    to {
        margin-bottom: 0px;
    }
}

.section-container {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    width: 100%;
    height: 100%;
    position: relative;

    margin: 0;
    padding: 0;

    display: -ms-flexbox;

    display: flex;
    -ms-flex-flow: column nowrap;
