    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    
    background: none;
}

.page-container {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    width: 100%;
    height: 100%;

    margin: 0;
    padding: 0;

    display: -ms-flexbox;

    display: flex;
    -ms-flex-flow: row nowrap;
        flex-flow: row nowrap;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex: 1 1 auto;
        flex: 1 1 auto;
}

.page-container.bottom {
    -webkit-animation-name: bottom-enter;
            animation-name: bottom-enter;
    -webkit-animation-duration: 0.8s;
            animation-duration: 0.8s;
}

.page-container.top {
    -webkit-animation-name: top-enter;
            animation-name: top-enter;
    -webkit-animation-duration: 0.8s;
            animation-duration: 0.8s;
}



.background-layer {
    display: inline-block;
    position: absolute;
    -o-object-fit: fill;
       object-fit: fill;
    -o-object-position: center;
       object-position: center;
    height: 100%;
}

@media only screen and (min-width: 960px) {
    #view-container {    
        display: -ms-flexbox;    
        display: flex;
        -ms-flex-flow: row nowrap;
            flex-flow: row nowrap;
        -ms-flex-pack: center;
            justify-content: center;
        -ms-flex-align: center;
            align-items: center;
    }
}





